<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8 mybg"></div>
      <div class="col-lg-4 login-form">
        <loginform v-show="login"></loginform>
        <signupform v-show="!login"></signupform>
        <div class="sign-up" v-show="login">
          <p>Haven't sign up yet???</p>
          <button class="btn btn-secondary btn-sm" @click="signup">Sign up</button>
        </div>
        <div class="sign-up" v-show="!login">
          <button class="btn btn-secondary btn-sm" @click="signup">Go back</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loginform from "../components/loginform";
import signupform from "../components/signupform";
export default {
  name: "loginPage",
  data: () => {
    return {
      login: true
    };
  },
  components: {
    loginform,
    signupform
  },
  methods: {
    signup() {
      if (this.login) {
        this.login = false;
      } else {
        this.login = true;
      }
    }
  }
};
</script>

